import React from 'react'

const ContactForm = (props) => (
    <form method="post" action="https://formspree.io/asdfasdf">
        <label>
           Name:
           <input type="text" name="_subject" />
        </label> 

        <label>
            E-Mail*: 
            <input type="email" name="_replyto" required/>
        </label>

        <label>
            Ich bin*: 
            <select name="_target" required>
                <option disabled selected value=""> -- Wählen -- </option>
                <option value="Dolmetscher">Dolmetscher</option>
                <option value="Auftraggeber">Auftraggeber</option>
                <option value="Organisation">Organisation</option>
            </select>        
        </label>

 
        <label>
            Nachricht*:
            <textarea type="text" name="_message" required/>            
        </label> 
        <input type="submit" value="Senden"></input>
    </form>
)

export default ContactForm