import React from 'react'
import Helmet from 'react-helmet'
import carlosPic from '../assets/images/team/carlos.jpeg'
import gioelePic from '../assets/images/team/gioele.jpg'
import nicolasPic from '../assets/images/team/nicolas.jpg'
import Header from '../components/Header'
import Layout from '../components/layout'
import ContactForm from '../components/ContactForm'

class Index extends React.Component {

  render() {
    return (
      <Layout>
        <Helmet title="Dolmy24 - Dolmetscher 24/7" />

        <Header />

        <div id="main">

        <section id="intro" className="main special">
            <h1>Hier kommen Sie schnell und einfach zu den besten Dolmetschern</h1>
          </section>

          <section id="benefits" className="main special">
            <header className="major">
              <h2>Wir sind die Lösung</h2>
            </header>
            <iframe title="Dolmy presentation video" src="https://player.vimeo.com/video/387499103" width="100%" height="500" frameborder="0" allow="autoplay; fullscreen" allowfullscreen></iframe>
          </section>

          <section id="benefits" className="main special">
            <header className="major">
              <h2>Das sind die Vorteile</h2>
            </header>
            <ul className="features">
              <li>
                {/* <span className="icon major style5 fa-hourglass"></span> */}
                <h2>Dolmetscher</h2>
                <p><b>Volle Kontrolle</b> von Kalender, Buchungen, Aufträge, Honorar und weitere administrative Belangen.</p>
              </li>
              <li>
                {/* <span className="icon major style5 fa-calendar"></span> */}
                <h2>Auftraggeber</h2>
                <p><b>Schnell</b> und <b>einfach</b> den <b>besten</b> Dolmetscher finden! Alles administrative überlassen sie uns!</p>
              </li>
              <li>
                {/* <span className="icon major style5 fa-copy"></span> */}
                <h2>Organisationen</h2>
                <p><b>Quality Control Management Tools</b>. So wie viele weitere Tools um nur die besten zu haben.</p>
              </li>
            </ul>
          </section>

          <section id="contact" className="main special">
            <header className="major">
              <h2>Kontaktieren Sie uns für weitere Informationen.</h2>
            </header>
            <ContactForm />
          </section>

          <section id="team" className="main special">
            <header className="major">
              <h2>Team</h2>
            </header>
            <ul className="features">
              <li>
                <span className="image"><img src={nicolasPic} alt="" style={{ borderRadius: `100%`, height: "13em" }} /></span>
                <h3>Nicolas Berlinger</h3>
                <p>Business Lead</p>
              </li>
              <li>
              <span className="image"><img src={gioelePic} alt="" style={{ borderRadius: `100%`, height: "13em" }} /></span>
                <h3>Gioele Moretti</h3>
                <p>Technical Lead</p>
              </li>
              <li>
              <span className="image"><img src={carlosPic} alt="" style={{ borderRadius: `100%`, height: "13em" }} /></span>
                <h3>Carlos Chida</h3>
                <p>Technical Lead</p>
              </li>
            </ul>
          </section>

        </div>
      </Layout>
    )
  }
}

export default Index
